<template>
  <div @click="change">{{title}}</div>
</template>

<script>
import { ref, toRefs } from '@vue/reactivity'
import { onMounted, watch } from '@vue/runtime-core'
export default {
  name: 'ly-comp',
  components:{},
  props:{
    title: {
      type: String,
      default: '标题'
    }
  },
  setup(props){
    const {title} = toRefs(props);
    onMounted(() =>{console.log(title.value);})
    const t2 = title;
    let num = ref(1);
    watch(num, (newVla, oldVal) =>{
      console.log(newVla, oldVal)
    })
    function change(){
      num.value ++
      console.log('change', num.value)
    }
    return {
      t2,
      change
      };
  }
}
</script>

